<template class='result'>
    <div>
      <van-cell-group>
        <van-field
          :value="nExamText"
          clickable
          label=""
          placeholder=""
          @click="openPicker('cOccupation')"
          required
          disabled
        />
        <van-field
            v-model="cIdCard"
            label=""
            placeholder=""
            clearable
            required
        />
        <van-field
            v-model="cName"
            clearable
            label=""
            placeholder=""
        />
        <van-field
            v-model="cPhone"
            label=""
            placeholder=""
            clearable
            type='tel'
        />
        <van-popup v-model="picker" position="bottom">
          <van-picker
          show-toolbar
          :columns="pickColumns"
          @cancel="picker = false"
          @confirm="onConfirm"
        />
        </van-popup>
    </van-cell-group>
    <div class="result-buttom-top"></div>
    <van-button type="info" :loading = 'loadingShow' loading-text="..." size='large' @click="query()"></van-button>
    </div>
</template>
<script>
import Vue from 'vue'
import { Cell, CellGroup, Image, Field, Button, ActionSheet, Toast, Dialog } from 'vant'
import { axiosPost, axiosGet } from '../../comment/http'
import api from '../../comment/api'
import qs from 'qs'
Vue.use(Cell).use(CellGroup).use(Image).use(Field).use(Button).use(Toast).use(Dialog).use(ActionSheet)
export default {
  name: 'result',
  data () {
    return {
      picker: false,
      cIdCard: '',
      cName: '',
      cPhone: '',
      pickColumns: [],
      loadingShow: false,
      nExamText: '',
      nExamId: ''
    }
  },
  created () {
    this.getAllExam()
  },
  mounted () {
    document.title = ''
  },
  methods: {
    getAllExam () {
      axiosGet(`${api.allExam}`)
        .then((data) => {
          if (data.code === 0) {
            data.data.map((item) => {
              this.pickColumns.push({'text': item.dictLabel, 'value': item.dictValue})
            })
            data.data.map((item, index) => {
              if (index === 0) {
                this.nExamText = item.dictLabel
                this.nExamId = item.dictValue
              }
            })
          }
        })
    },
    openPicker () {
      this.picker = true
    },
    onConfirm (picker, value, index) {
      this.picker = false
      this.nExamText = picker.text
      this.nExamId = picker.value
    },
    query () {
      // if (this.cName === '') {
      //   this.$toast('')
      //   return false
      // }
      if (this.cIdCard === '' || !(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.cIdCard))) {
        this.$toast('')
        return false
      }
      if (this.cPhone && !(/^1[3456789]\d{9}$/.test(this.cPhone))) {
        this.$toast('')
        return false
      }
      this.loadingShow = true
      axiosPost(`${api.querySignUp}`, qs.stringify({
        cName: this.cName,
        cIdCard: this.cIdCard,
        cPhone: this.cPhone,
        nExamId: this.nExamId
      }))
        .then((data) => {
          this.loadingShow = false
          if (data.code === 0) {
            this.$router.push({name: 'Result', params: data.data})
          } else {
            this.$toast(data.msg)
            Dialog.alert({
              title: '',
              message: data.msg
            })
          }
        })
    }
  }
}
</script>
<style>
  .van-field__control:disabled {
    color: #2a2a2a !important;
    -webkit-text-fill-color: #2a2a2a !important;
  }
</style>
<style lang="scss" scoped src="./index.scss">
</style>



