<template>
  <div class="notice">
    <div class="road">
      <van-pull-refresh v-model="isDownLoading" @refresh="onDownRefresh">
      <van-list
        v-model="isUpLoading"
        :finished="upFinished"
        finished-text=""
        :immediate-check="false"
        :offset="10"
        @load="onLoadList"
      >
        <div v-if="dtWinNumberInfos.length > 0" >
          <div v-for="(activity, index) in dtWinNumberInfos"
              :key="index"
              :hide-timestamp="true"
              color="#0bbd87"
              class="allcontent">
              <div class="timeWrapper" @click="handleRoadDetail(activity)">
                <div class="titleText" v-html="activity.noticeTitle"></div>
                <div class="time">{{ activity.createTime }}</div>
              </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { List, PullRefresh, Divider } from 'vant'
import { axiosGet } from '../../comment/http'
import api from '../../comment/api'
import qs from 'qs'
// import qs from 'qs'
Vue.use(List).use(PullRefresh).use(Divider)
export default {
  name: 'result',
  data () {
    return {
      nullTip: '~',
      pageSize: 10, // 
      pageIndex: 1, // 
      dtWinNumberInfos: [], // 
      isDownLoading: false, // 
      isUpLoading: false, // 
      upFinished: false, // 
      offset: 100 //  offset load
    }
  },
  mounted () {
    document.title = ''
    this.getroadList()
  },
  methods: {
    // 
    getroadList () {
      const _self = this
      axiosGet(`${api.noticeList}?pageNum=${_self.pageIndex}&pageSize=${_self.pageSize}`).then(response => {
        if (response.data.code === 0) {
          const rows = response.data.rows
          if (rows == null || rows.length === 0) {
            // 
            _self.upFinished = true
            return
          }
          if (rows.length < _self.pageSize) {
            // 10
            _self.upFinished = true
          }
          // 
          if (_self.pageIndex === 1) {
            _self.dtWinNumberInfos = rows
          } else {
            _self.dtWinNumberInfos = _self.dtWinNumberInfos.concat(rows)
          }
        }
      }).catch(error => {
        this.$message({
          showClose: true,
          message: '{' + error + '}',
          type: 'error'
        })
      }).finally(() => {
        _self.isDownLoading = false
        _self.isUpLoading = false
      })
    },
    onDownRefresh () {
      this.pageIndex = 1
      this.upFinished = false // 
      this.getroadList() // 
    },
    // 
    onLoadList () {
      if (this.upFinished) {
        this.pageIndex++
        this.getroadList()
      }
    },
    // 
    handleRoadDetail (activity) {
      this.$router.push({
        path: 'noticeDetail',
        query: {
          content: qs.stringify(activity)
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped src="./index.scss">
</style>



