import axios from 'axios'

/**
 * 
 * @param {string} URL 
 */
export function axiosGet (URL) {
  return new Promise((resolve, reject) => {
    axios.get(URL)
      .then((response) => {
        resolve(response.data)
      })
      .catch((error) => {
        reject(error)
      })
  })
}
/**
 * 
 * @param {string} URL 
 */
export function axiosPost (URL, params) {
  return new Promise((resolve, reject) => {
    axios.post(URL, params)
      .then((response) => {
        resolve(response.data)
      })
      .catch((error) => {
        reject(error)
      })
  })
}



